<template>
  <div>
    <br />
    <label>تعليمات الأمتحان القادم</label>
    <p>قم بوضع فواصل (,) بين الجمل اللي تريد فصلها على حدى</p>
    <hr />
    <v-textarea
      style="background: #fff7"
      v-model="instructions"
      clearable
      clear-icon="cancel"
      label="Text"
      auto-grow
      palce-holder="This is clearable text."
    ></v-textarea>
    <v-btn class="v-btn mt-5" @click="sendInstructions">انشر</v-btn>
  </div>
</template>

<script>
import axios from "axios";

export default {
  created() {
    axios.get("/getAdminData").then((res) => {
      this.instructions = res.data.adminData.nextExamInstructions;
    });
  },
  data() {
    return {
      instructions: "",
    };
  },
  methods: {
    sendInstructions() {
      axios.post("/sendInstructions", { instructions: this.instructions }).then((res) => {
        if (res.data.done) {
          this.$store.dispatch(
            "writemessage",
            "Next exam instructions sent successfully"
          );
        }
      });
    },
  },
};
</script>

